				<view class="list-item-wrap">				
					<view class="list-item">				
						<view class="cover-wrap">
							<img class="cover" :src="baseUrl+item.imgUrl"></img>
						</view>
						<view class="name">{{item.name}}</view>
					</view>
				</view>
			</block>
			
		</view>
		<!-- 奖品列表end -->
		
		<view class="rule-wrap">
			<view class="title">活动规则说明：</view>
			<view class="rule-content">
				1.本活动使用微信登录和微信支付。<br/>
				2.选择心仪的盲盒系列，每次需要支付金额购买。<br/>
