<script>
	export default {
		data() {
			return {
				prizeList: null, //奖品列表
				baseUrl: this.$url.baseUrl,  //图片基本路径
			}
		},
		onLoad(option){
		    const prizePoolId = JSON.parse(decodeURIComponent(option.prizePoolId));
			console.log(prizePoolId);
			
			this.getPrizePoll(prizePoolId);
			
			console.log(this.baseUrl);
		},
		methods: {
			async getPrizePoll(prizePoolId) {
				
				let res = await this.$request({
					url: this.$url.prizePollUrl + prizePoolId,
				});
	
				console.log(res);
				console.log(res.data.data.prize);
				
				this.prizeList = res.data.data.prize;
				
			}
		}
	}
</script>

